.dashboard-container {
    display: flex;
  }
  
  .sidebar {
    width: 200px;
    background-color: #2e1e77;
    padding: 20px;
  }
  
  .sidebar-menu button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #2e1e77;
    color: white;
    border: none;
    text-align: left;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .sidebar-menu button:hover {
    background-color: white;
    color: black;
  }
  
  .main-content {
    flex-grow: 1;
    padding: 20px;
    background-color: #f0f2f5;
  }
  
  .content-section {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .stats-and-pie-container {
    display: flex;
    justify-content: space-between;
    gap: 10px; /* Reduced gap between counters and pie charts */
    flex-wrap: wrap;
  }
  
  .flex {
    display: flex;
  }
  
  .flex-col {
    flex-direction: column;
  }
  
  .gap-6 {
    gap: 6px;
  }
  
  .counter-card {
    background-color: white;
    padding: 30px; /* Increased padding */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    flex: 1; /* Ensure counters take up available space evenly */
    display: flex;
    flex-direction: row; /* Align h2 and p horizontally */
    justify-content: space-between; /* Align h2 to the left and p to the right */
    align-items: center;
    height: 150px; /* Increased height for larger counter cards */
    width: 420px; /* Increased width */
  }
  
  .counter-card h2 {
    font-size: 25px; /* Increased font size for the title */
    font-weight: bold;
    color: #333; /* Color for the title */
  }
  
  .counter-card p {
    font-size: 50px; /* Increased font size for the counter value */
    font-weight: bold;
    color: #2e1e77; /* Match the sidebar color for consistency */
  }
  .counter-card:hover {
    transform: scale(1.05); /* Animation effect on hover */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
  
  .total-average-section {
    margin-top: 40px;
    background-color: white;
    padding: 30px; /* Increased padding */
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    display: flex;
    flex: 1;
    justify-content: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    align-items: center; /* Align items to the start of the section */
    flex-direction: column; /* Ensures vertical alignment of all content */
}

.total-average-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; /* Ensure all text is centered */
}

.total-average-card h3 {
    font-size: 32px; /* Increased font size */
    margin-bottom: 10px;
}

.total-average-card .average-value {
    font-size: 48px; /* Increased font size for total average */
    font-weight: bold;
    color: #2e1e77; /* Matching the sidebar color for brand consistency */
    margin-top: 10px; /* Added space above average value */
}

.total-average-details {
    display: flex;
    flex-direction: column; /* Stack the data vertically */
    gap: 10px; /* Adjusted gap between data entries */
    margin-top: 20px; /* Added space above the details section */
}

.total-average-details div {
    text-align: center;
}

.total-average-details p {
    font-size: 26px; /* Increased font size for better readability */
    color: #666;
}

.total-average-section:hover {
    transform: scale(1.02); /* Slight hover animation */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

  .pie-charts {
    display: flex;
    justify-content: space-between;
    gap: 15px; /* Reduced gap */
  }
  
  .pie-chart-card {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    align-items: center;
    justify-content: center;
    width: 90%; /* Increased the width to make pie charts larger */
    transition: transform 0.3s ease, box-shadow 0.3s ease, width 0.5s ease;
  }
  .pie-chart-card h2{
    font-size: 32px;
  }
  
  .pie-chart-card:hover {
    transform: scale(1.1); /* Slightly bigger hover effect */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
  
  /* Align the sales chart and notifications in a horizontal flex container */
  .sales-and-notifications {
    display: flex;
    gap: 20px; /* Adjust gap between the chart and notifications */
}

.sales-chart {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    flex-grow: 0.5; /* Reduced size of the line chart */
    transition: transform 0.3s ease, box-shadow 0.3s ease, flex-grow 0.5s ease;
}
.sales-chart h2{
    font-size: 32px;
}

.sales-chart:hover {
    transform: scale(1.05); /* Animation effect on hover */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.notifications {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    flex-grow: 1; /* Increased width of notifications */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    width: fit-content;
    overflow-y: auto; /* Enable scrolling if content exceeds max height */
    max-height: 600px;
}

.notifications h2 {
    font-size: 32px;
    margin-bottom: 40px; /* Space below the heading */
}

.notifications ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.notifications ul li {
    background-color: #f9f9f9; /* Light background for each notification */
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 10px; /* Space between notifications */
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for separation */
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.notifications ul li:hover {
    background-color: #f1f1f1; /* Slight change in background color on hover */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Stronger shadow on hover */
}

.notifications ul li .notification-title {
    font-size: 24px; /* Title size */
    font-weight: bold;
    color: #333; /* Dark color for the title */
}

.notifications ul li .notification-details {
    font-size: 18px; /* Message size */
    color: #555; /* Slightly lighter color for message */
    margin-top: 10px; /* Space between title and message */
}

.notifications:hover {
    transform: scale(1.02); /* Animation effect on hover */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}


  